.sidebar {
    padding: 16px;
    border: 1px solid #ddd;
    border-radius: 8px;
    background-color: #f9f9f9;
  }
  
  .filter-section, .sort-section {
    margin-bottom: 16px;
  }
  
  .rating-label {
    display: flex;
    align-items: center;
    cursor: pointer;
    margin-bottom: 8px;
  }
  
  .rating-input {
    margin-right: 8px;
  }
  
  .rating-label span {
    color: #ffcc00; /* Star color */
  }
  
  .sort-label {
    display: flex;
    align-items: center;
    cursor: pointer;
    margin-bottom: 8px;
  }
  
  .sort-input {
    margin-right: 8px;
  }
  
  @media (max-width: 600px) {
    .sidebar {
      width: 90%;
      display: flex;
      flex-direction: row;
      justify-content: space-around;
    }
  }
  